import React,{Component} from 'react';
import axios from 'axios';
import PropTypes from 'prop-types';
import {reactLocalStorage} from 'reactjs-localstorage';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import Switch from '@material-ui/core/Switch';

const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    formControl: {
      margin: theme.spacing.unit,
      minWidth: 150,
    },
    selectEmpty: {
      marginTop: theme.spacing.unit * 0,
    },
  });

class BrandAdd extends Component{
    constructor(props){
        super(props);
        this.state = {
            name : "",
            showOnWeb : "Y",
            checked : true
         
        }

    }
    
    handleChecked = name => event => {
        this.setState({ [name]: event.target.checked });
    };

    handleChange = event => {
        this.setState({ [event.target.name]: event.target.value });
    };

    submitHandler = (event) => {
        if(this.state.checked === true){
            var show = 'Y';
        }
        else{
            var show = 'N';
        }
        const payload = {
            "name": this.state.name,
            "showOnWeb" : show,
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/brand/new', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            window.location.href='#/admin/brand';
        })
        .catch((error) => {
            console.log(error);
        });  
        event.preventDefault();     
    }

    render(){
        return(
            <article className="article">
                <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Add New Brand</h6>
                    </div>
                    <form  onSubmit={this.submitHandler}>
                    
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Brand Name</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'name'}}
                            onChange={this.handleChange}
                            value={this.state.name}
                            placeholder="Brand Name"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>
                   
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Show On Web</label>
                        <div className="col-md-10">
                        <Switch
                            checked={this.state.checked}
                            onChange={this.handleChecked('checked')}
                            value={this.state.showOnWeb}
                            color="primary"
                            />
                        </div>
                    </div>
                    

                    <div className="form-group row">
                        <div className="col-md-2"></div>
                        <div className="col-md-10">
                        <Button variant="contained" className="btn-w-md" type="submit"> Submit </Button>
                        </div>
                    </div>
                    </form>

                </div>
                </div>
            </article>
        )
    }
}
BrandAdd.propTypes = {
    classes: PropTypes.object.isRequired,
  };
export default  withStyles(styles)(BrandAdd);